import React, { Component } from 'react'
import '../styles/ImagesGallery.css'

class ImagesGallery extends Component {  
    constructor(props) {
        super(props)
    }

  render() {
    const { children } = this.props
    return (
        <div className="imagesGallery">
            { children.map(i => i) }
        </div>
    )
  }
}

export default ImagesGallery